import React, {useState} from 'react'
import FeedBack from "../../feedback"
import styled from "styled-components";
import Hr from '../../hr';
import style from './about.module.css'
import Img from './ourRoom1.jpg'

const AboutWrapper = styled.div`
text-align:center;
padding:60px 0;

`

const WrapperBackgroundColor = styled.div `
background-color :#ffff;
padding:25px 0;
text-align:left;
padding-left: 15px;
p{
    font-size:18px;
}
.alertAbout{
    font-size:18px;
    color:#1e3953;
    font-weight:bold;
    text-align:center;
}
div{
    width:100%;
    display:flex;
    justify-content:center;
}
img{
    height:420px;
}
`
const Titile = styled.p `
font-size:1.8rem;
font-weight: 500;
font-family: 'Roboto', sans-serif;
`

const aboutPage = (props) => {
    return(
        <AboutWrapper>
        <Titile>Сведения о компании «Центр-юрист»</Titile>
        <Hr></Hr>
        <WrapperBackgroundColor>
        <p>Юридическая компания «Центр-юрист» - московская  юридическая компания основана в 2017 году опытными юристами и адвокатами. На сегоднейший день в нашей компании работают опытные юристы, которые успехи зарекомендовать себя как эксперты в различных отраслях права. Мы начинали свою деятельность в маленьком составе и сами нарабатывали свою репутацию. Сейчас мы — надёжная юридическая компания, где работают признанные специалисты своего дела.</p>
        <p>Мы оказываем только высокопрофессиональные юридические услуги, потому что у нас имеется очень весомый практический опыт в юридической сфере и прочная юридическая база. Солидный опыт и высокая квалификация наших юристов позволяют справиться с поставленной задачей с минимальными временными и денежными потерями. Мы гарантируем, что со своей стороны приложим все возможные усилия, чтобы помочь вам.</p>
        <p className="alertAbout">Если нуждаетесь в квалифицированной правовой защите – мы будем рады помочь!</p>
        <div>
        <img src={Img}></img>
        </div>
        </WrapperBackgroundColor>
        </AboutWrapper>

    )
}

export default aboutPage 