import React from "react";
import styled from "styled-components";
import { Field, reduxForm } from "redux-form";
import {phoneNumber, email, minLength5, requiredForName, requiredForTextArea } from "../component/utils/validators";
import { Input,Textarea  } from "../component/form/formsControls";
import axios from 'axios'
import Details from './form/details'

const Wrapper = styled.div`
display:flex;
justify-content:center;

}`

const ButtonStyle = styled.button`
letter-spacing:2px;
padding: 15px 55px;
font-size: 14px;
font-weight: 700;
border-radius: 5px;
border: 2px solid;
background-color: #92c73e !important;
border-color: #92c73e;
align-self: center;

}`

class Info extends React.Component {

  constructor(props) {
    super(props)
    this.state = {
        page: 1,
        data: []
    }

  }

  async handleFormSubmit(values) {
  const username = values.username
  const email = values.email
  const phone = values.phone
  const textarea = values.textarea
  const response = await axios.post('http://localhost:3001/send', {
    username: username,
      email: email,
      phone: phone,
      textarea:textarea
  })
}
onSubmit = (values) => {
  this.handleFormSubmit(values)
  this.setState({ page: this.state.page + 1 })

}


  render() {

    const handleSubmit = this.props.handleSubmit;
      return (
        <Wrapper>
          {this.state.page === 2 && <Details dataForDetails={this.state.data} /> }
          {this.state.page ===1 &&
        <form onSubmit={handleSubmit(this.onSubmit)} style={{'width':'100%', 'display': 'flex', 'justify-content': 'center' , 'flex-direction':'column'}}>
          <Field
            name="username"
            type="text"
            component={Input}
            label="Ваше имя*"
            placeholder={'Имя'}
            validate={requiredForName}  
          />
          <Field
            name="phone"
            type="text"
            component={Input}
            placeholder="+7(999)222-33-44"
            label="Контактный номер*"
            validate={[phoneNumber]}
          />
         <Field
            name="email"
            type="email"
            component={Input}
            placeholder="адрес@почта.ру"
            label="Email(необязательно)"
            validate={email}
          />
          <Field
            name="textarea"
            type="text"
            component={Textarea}
            label="Описание проблемы*"
            validate={[minLength5, requiredForTextArea]}
          />
          <ButtonStyle>Получить консультацию</ButtonStyle>
        </form>
  }
        </Wrapper>
      )
  }
}



export default reduxForm({
  form: 'companyData',
})(Info)