import React, {useState} from 'react'
import FeedBack from "../../feedback"
import styled from "styled-components";
import Hr from '../../hr';
import style from './about.module.css';
import Img from './info-img.jpg';
import Row from "react-bootstrap/Container";
import Col from "react-bootstrap/Col";


const AboutWrapper = styled.div`
text-align:center;
padding:60px 0;

`

const WrapperBackgroundColor = styled.div `
background-color :#ffff;
padding:25px 0;
text-align:left;
padding-left: 15px;
overflow:hideen;
p{
    font-size:16px;
}
.alertAbout{
    font-size:18px;
    color:#1e3953;
    font-weight:bold;
    text-align:center;
}
div{
    width:100%;
    display:flex;
    justify-content:center;
}
img{
    max-height:450px;
}
`
const Titile = styled.p `
font-size:1.8rem;
font-weight: 500;
font-family: 'Roboto', sans-serif;
`
const ContactInfo = styled.div`
display:flex;
flex-direction:column;
text-align:left;
margin-left:45px;
h1{
font-size:1.4rem;!important;
margin:20px 0;
}
`
const ContactParaph= styled.div`
display:flex;
flex-direction:column;

h1{
    text-align:center;
}
`
const СontactSpan = styled.span`
color: #92c73e;
font-weight: bold;
font-size:1.8rem;
text-align:center;
`
const СontactInfoMap = styled.span`
margin-top:25px;
display:block;

`




const aboutPage = (props) => {
    return(
        <AboutWrapper>
        <Titile>Контактная информация о компании «Центр-юрист»</Titile>
        <Hr></Hr>
        <WrapperBackgroundColor>
        <Row>
        <Col lg={6}>
            <img src={Img}></img>
        </Col>
        <Col lg={6}>
            <ContactInfo>
                <ContactParaph>
                    <СontactSpan>Адрес</СontactSpan>
                    <h1>«Центр-ЮРИСТ»</h1>
                </ContactParaph>
                <ContactParaph>
                    <p>115172, г. Москва, метро Таганская, ул. Большие Каменщики, дом 6, стр. 1.</p>
                    <p>ОГРН: 1187746613757 </p>
                    <p>ИНН/КПП: 7720435015  / 772001001</p> 
                    <p>E-mail: ayvars@bk.ru</p>
                    <p>Телефон: +7(926) 027 66 58</p>         
                    <p>Часы работы:</p> 
                    <p>Пн-Пт: 09:00–20:00</p> 
                    <p>Суб-Вс:09:00–20:00(Телефон) </p> 

                </ContactParaph>
            </ContactInfo>
        </Col>
        </Row>
        
        <СontactInfoMap><strong>Как добраться:</strong> с Ленинградского проспекта, повернуть направо, к центральному входу в гостиницу «Аэростар», доехав до центрального входа в гостиницу, повернуть налево и потом направо.</СontactInfoMap>
        </WrapperBackgroundColor>
        </AboutWrapper>

    )
}

export default aboutPage 