import React, {useState} from 'react'
import FeedBack from "../../feedback"
import styled from "styled-components";
import Hr from '../../hr';
import style from './consultation.module.css'

const ConsultationWrapper = styled.div`
text-align:center;
padding:60px 0;
width:100%;
form{
    align-self:center;
div{
justify-content:center;
}
button{
    background: #92c73e;
  }
}`

const WrapperBackgroundColor = styled.div `
background-color :#ffff;
padding:25px 0;
p{
    font-size:18px;
    font-family: 'Roboto', sans-serif;
}
`
const ConsultationPage = (props) => {
    return(
        <ConsultationWrapper>
        <h1>Бесплатная юридическая консультация</h1>
        <Hr></Hr>
        <WrapperBackgroundColor>
        <p>Вы можете получить юридическую консультацию совершенно <strong>бесплатно</strong>. <br></br><strong>Задайте вопрос</strong> прямо сейчас, и наши опытные юристы свяжутся с Вами уже через <strong>15</strong> минут!<br></br>
        </p>
        <FeedBack/>
        </WrapperBackgroundColor>
        </ConsultationWrapper>
    )
}

export default ConsultationPage 