import React from 'react';
import Container from 'react-bootstrap/Container';
import styled from 'styled-components';
import Col from 'react-bootstrap/Col';
import Row from 'react-bootstrap/Row';
import Map from '../map/index.js';
import style from "./link.module.css";
import {NavLink} from 'react-router-dom';
import VkLogo from './vk.svg';
import InstagramLogo from './instagram.svg';
import TwitterLogo from './twitter.svg';

const w = styled.div`
display:flex;
flex-direction: column;
flex-basis: 50%;
max-width: 50%;
text-align:center;
z-index:101;
`
const FooterForMobile = styled.div`
display:none;
font-family: 'Roboto', sans-serif;
letter-space:1px;
font-size:16px;
text-align:center;
font-weight: normal;
    
    p{
        margin-top: 15px;
    }

    span{
        padding-bottom:15px;
        color: #92c73e !important;
font-size:1.3rem;
font-weight: 500;
        display:block;
        border-bottom: 1px solid rgba(82, 229, 231, 0.3);
    }
@media (max-width: 768px) {
    display: block;
  }
`
const FooterMain = styled.div`
margin-bottom: 15px;
display:flex;
a::before{
    content: '\\21D2';
    color: #92c73e;
    margin-right:7px;
    font-size: 1.2rem;
    }
a{
        font-size: 1rem;
        line-height: 1rem;
    }
img{
    width:1.5rem;
    align-self:flex-end;
}

.footerWrap {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 100%;
}

@media (max-width: 768px) {
    display: none;
  }

`

const FooterTitle = styled.span`
color: #92c73e !important;
font-size:1.3rem;
text-align:left;
font-weight: 500;
padding-left:25px;
display:block;
margin-bottom:10px;
`




class App extends React.Component{
    render(){
        return(
            <React.Fragment>
                <Map/>
                <div className="backgroundWrapper footer">
                   <Container style={{"color":'#fff'}}>
                       <Row>
                           <FooterMain>
                           <Col sm={6} style={{"text-align":"left", 
                                        "border-right": "1px solid rgba(82,29,231, 0.3)",
                                    }} >
                                <div><FooterTitle>Центр-юрист</FooterTitle>
                                    <p style={{'font-size': '1rem'}}>Оказание юридических услуг на профессиональной основе высококвалифицированными юристами</p></div>
                                    <div>+8 (800)-555-35-35</div>
                                <div>support@websites.ru</div>
                           </Col>
                           <Col  className="footerWrap" sm={4}>
                           <NavLink className={`${style.font}`}  activeClassName={`${style.active}`} to="/consultation">Бесплатная консультация</NavLink>
                        <NavLink className={`${style.font}`}  activeClassName={`${style.active}`} to="/about">О нас</NavLink>
                        <NavLink className={`${style.font}`}  activeClassName={`${style.active}`} to="/price">Цены</NavLink>
                        <NavLink className={`${style.font}`}  activeClassName={`${style.active}`} to="/contact">Конакты</NavLink>
                           </Col>
                           <Col style={{'flex-direction':'row-reverse !important'}} sm={2}> 
                           <div className="footerWrap">
                           <img src={VkLogo}></img>
                           <img src={InstagramLogo}></img>
                           <img src={TwitterLogo}></img>
                           </div>
                           </Col>
                           </FooterMain>
                       </Row>
                       <Row>
                            <Col>
                                <FooterForMobile>
                                    <span>Центр-юрист</span>
                                    <p>Оказание юридических услуг на профессиональной основе высококвалифицированными юристами</p>
                                    <p>+7(926)-027-66-58</p>
                                    <p>Support@center-yurist.ru</p>
                                </FooterForMobile>
                                <p style={{"border-top": "2px solid rgba(82,229,231, 0.3", 'font-size':'13px', 'text-align':'center','color': 'rgba(255,255,255,.56)', 'font-size': '10px', 'line-height': '16px', 'padding-top':'10px'}}>Настоящий сайт и представленные на нем материалы носят исключительно информационный характер и ни при каких условиях не являются публичной офертой, определяемой положениями Статьи 437 ГК РФ .</p>
                           </Col>
                       </Row>
                    </Container> 
                </div>
            </React.Fragment>
        )
    }
  }
  
  
  export default App;