import React, {useState, useEffect} from 'react';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/col';
import styled from 'styled-components';
import {CursorFill, Clock, Phone} from 'react-bootstrap-icons';
import Popup from '../header-bottom/popup.js'

const Coordinat = styled.div`
@media (max-width: 768px) {
    display: none;
  }
`

const ButtonStyle = styled.button`
border:none;
text-decoration:underline;
font-weight: 500;
color:#fff;

&:hover{

}`

const Header = (props) => {

    const Address = (props) => {
        return(
            <span>{props.addr}</span>
            )
    }
    const Time = (props) => {
        return(
            <span>{props.time}</span>
            )
    }

    const TimeCallBackButton = (props) => {
        return(
            <span>{props.CallBackButton}</span>
            )
    }
    const Number = (props) => {
        return(
            <span>{props.Number}</span>
            )
    }
    const TimeWork = (props) => {
        return(
            <span>{props.TimeWork}</span>
            )
    }
    const [scrolled,setScrolled]= useState(false);
    const handleScroll = () => {
        const offset=window.scrollY;
        if (offset > 300){
            setScrolled(true);
        }
        else {
            setScrolled(false);
        }
    }

  
  

    useEffect(() => {
        window.addEventListener('scroll', handleScroll)

            function addClass(){
            let navBarDom = document.querySelector('.headerRow');
            if(scrolled){
                navBarDom.classList.add('scrolled')
            }
            else{
                navBarDom.classList.remove('scrolled')
            }
        }
        addClass();

    })
    

    const [openPopup, setOpenPopup] = useState(false)
    return(
        <Row className={"d-lg-flex flex-nowrap headerRow"}>
            <Col className="d-none d-lg-block" lg={3}>
                <CursorFill color="#92c73e" size={16}/>
                <Address addr="м.Таганская, ул. Большие Каменщики, дом 6, стр. 1"/>
            </Col>
            <Col className="d-none d-lg-block" lg={3}>
                <Clock color="#92c73e" size={16}/>
                <Time time= "Ежедневно, 9:00-18:00"/>
            </Col>
            <Col xs="6" lg={3}>
                <Phone color="#92c73e" size={16}/>
                <Time time= "+7 (926)-027-66-58"/>
            </Col>
            <Col  xs="6" lg={3}>
            <ButtonStyle onClick={() => setOpenPopup(true)}>Получить консультацию</ButtonStyle>
                <Popup show={openPopup}
                       onHide={() => setOpenPopup(false)}
                />
            </Col>
        </Row>
)

}






export default Header;
