
export const requiredForName = value => (value || typeof value === 'number' ? undefined : 'Пожалуйста, введите Ваше имя');

export const phoneNumber = value =>
  value && /^(\s*)?(\+)?([- _():=+]?\d[- _():=+]?){10,14}(\s*)?$/i.test(value)
    ? undefined
    : 'Формат +7(999)222-33-44'

export  const email = value =>
  value && !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(value)
    ? 'Формат aivanov@mail.ru'
    : undefined

    export const minLength = min => value =>
    ( value && value.length < min ? `Описание проблемы должно содержать не менее ${min} символов` : undefined) 
    
    
export const minLength5 = minLength(15)
  

export const requiredForTextArea = value => (value || typeof value === 'number' ? undefined : 'Пожалуйста, опишите Вашу проблему');

    