import React from 'react';
import { YMaps, Map, Placemark  } from 'react-yandex-maps';




const mapData = {
    center: [55.7375,37.6554],
            
    zoom: 16
  };
  
  const coordinates = [
    [55.684758, 37.738521],
    [57.684758, 39.738521]
  ]; 




class Maps extends React.Component{
  render(){
    return(
      <React.Fragment>
        <YMaps>
         <div className="footer-hr">115172, г. Москва, метро Таганская, ул. Большие Каменщики, дом 6, стр. 1.</div>
            <Map  instanceRef={ref => { ref && ref.behaviors.disable('scrollZoom'); }} height={'500px'} width={'100%'} defaultState={mapData}>  
                <Placemark  options={{ preset: 'islands#blueCourtIcon', iconColor:'#52e5e7',}} geometry={[55.7361,37.6550]} />
            </Map>
        <div className="footerbottom-hr"></div>
        </YMaps>
      </React.Fragment>
    )
  }
}


export default Maps;
