import React from 'react';
import styled from 'styled-components';
import style from "./link.module.css";
import {BrowserRouter, NavLink} from 'react-router-dom';

const Ul = styled.ul`
  list-style: none;
  z-index: 102;
  display: none;
  flex-flow: row nowrap;
  li {
    padding: 18px 10px;
  }
  @media (max-width: 992px) {
    flex-flow: column nowrap;
    background-color: #0D2538;
    display: flex;
    position: fixed;
    transform: ${({ open }) => open ? 'translateX(0)' : 'translateX(100%)'};
    top: 0;
    right: 0;
    height: 100vh;
    width: 300px;
    padding-top: 3.5rem;
    transition: transform 0.3s ease-in-out;
    li {
      color: #fff;
    }
  }
`;

const RightNav = ({ open }) => {
  return (
    <Ul className={`${style.wrapper}`} open={open}>
      <NavLink className={`${style.font}`}  activeClassName={`${style.active}`}  exact to="/">Главная</NavLink>
                        <NavLink className={`${style.font}`}  activeClassName={`${style.active}`} to="/consultation">Бесплатная консультация</NavLink>
                        <NavLink className={`${style.font}`}  activeClassName={`${style.active}`} to="/about">О нас</NavLink>
                        <NavLink className={`${style.font}`}  activeClassName={`${style.active}`} to="/price">Цены</NavLink>
                        <NavLink className={`${style.font}`}  activeClassName={`${style.active}`} to="/contact">Конакты</NavLink>
    </Ul>
  )
}

export default RightNav